/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --text:					#454545;
  --text-light:			rgba(69,69,69,.7);
  --app-color:			#fee298;
  --app-color-focus:		rgba(254,226,152,.5);
  --dividers:				#e6e6e6;
  --titlebar:				#454545;
  --header:				linear-gradient(#edebed, #d6d4d6) no-repeat border-box;
  --header-border:		#b5b4b5;
  --form-control:			#409BFF;
  --categories:			#edebed;
  --accent:				#3478f6;
  --toolbar-hover:		rgba(0,0,0,.05);
  --toolbar-border:		rgba(69,69,69,.5);
  --edit: 				#ffffff;
  --edit-hover:			rgba(69,69,69,.5);
  --edit-active:			rgba(69,69,69,.8);
  --modal:				rgba(240,239,240,.95);
  --window:				#ededed;
  --input-color:			#000000;
  --input-border:			#cccccc;
  --input-background:		#ffffff;
  --select:				#ffffff;
  --select-border:		#b2b2b2;
  --button:				#ffffff;
  --button-color:			rgba(0,0,0,.85);
  --button-border:		#c0c0c0;
  --button-hover:			linear-gradient(#6cb3fa, #067dff);
  --button-color-hover:	#ffffff;
  --button-border-hover:	linear-gradient(#50a4f9, #005cff);
  --link:					#1e90ff;
  --hover:				#005ee0;
  --code:					#e6e6e6;
  --code-color:			#777777;
  --mark:					#000000;
  --table-border:			#dddddd;
  --table-head:			#ffffff;
  --table-stripe:			rgba(69,69,69,.03);
}

[data-theme^="dark"] {
  --text:					#dcdcdc;
  --text-light:			rgba(255,255,255,.6);
  --app-color:			#c5993e;
  --app-color-focus:		#343434;
  --dividers:				#484848;
  --titlebar:				#c8c6c6;
  --header:				linear-gradient(#4b4b4b, #3d3d3d) no-repeat border-box;
  --header-border:		#000000;
  --form-control:			#4D89F5;
  --categories:			#1e1e1e;
  --toolbar-hover:		rgba(255,255,255,.05);
  --toolbar-border:		rgba(255,255,255,.3);
  --edit: 				#1e1e1e;
  --edit-hover:			rgba(255,255,255.5);
  --edit-active:			rgba(255,255,255.8);
  --modal:				rgba(56,56,56,.95);
  --window:				#343434;
  --input-color:			#dcdcdc;
  --input-border:			#565656;
  --input-background:		#292724;
  --select:				#616161;
  --select-border:		#7f7f7f;
  --button:				#616161;
  --button-color:			rgba(255,255,255,.85);
  --button-border:		#7f7f7f;
  --button-hover:			linear-gradient(#1768e5, #145ccc);
  --button-color-hover:	#ffffff;
  --button-border-hover:	#5895ec;
  --code:					#1e1e1e;
  --code-color:			#aaaaaa;
  --table-border:			#484848;
  --table-head:			#484848;
  --table-stripe:			rgba(255,255,255,.03);
}

:root {
  --texture: url('data:image/png;base64,');
  --texture2: url('data:image/png;base64,');
}

[data-theme="dark"] {
  --texture: url('data:image/png;base64,');
  --texture2: url('data:image/png;base64,');
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text);
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

body {
  text-align: center;
  padding: 0;
  background-image: var(--texture);
}

body header {
  height: 23px;
  background: var(--header);
  background-size: 100% 23px;
  border-bottom: 1px solid var(--header-border);
  -webkit-app-region: drag;
}

body main {
  padding: 10px 20px 0;
  height: 270px;
}

body main figure {
  padding-bottom: 10px;
}

body main figure img {
  -webkit-user-drag: none;
  -webkit-app-region: no-drag;
}

body main h1 {
  font-size: 15px;
  margin-bottom: 20px;
  color: var(--text);
  font-weight: 700;
}

body main p {
  font-size: 13px;
  font-weight: 400;
  width: 160px;
  margin: 0 auto 15px;
  color: var(--text);
}

body main dl {
  font-size: 11px;
  line-height: 14px;
  margin-bottom: 15px;
}

body main dl dt, body main dl dd {
  display: inline;
  color: var(--text);
}

body main dl dt {
  padding-right: 5px;
}

body main dl dd::after {
  display: block;
  content: '';
  white-space: pre-line;
}

body main button {
  padding: 3px 14px;
  border: 1px solid var(--button-border);
  background: var(--button);
  background-clip: border-box;
  color: var(--button-color);
  border-radius: 5px;
}

body main button:active {
  color: var(--button-color-hover);
  background-image: var(--button-hover);
  border: 1px solid;
  border-image: var(--button-border-hover);
  border-image-slice: 1;
}

@media screen and (-webkit-min-device-pixel-ratio: 2) {
  body {
    background-image: var(--texture2);
    background-size: 100px 100px;
  }
}

[data-theme="dark"] body main button {
  border-left: 0;
  border-right: 0;
  border-bottom: 0;
}

[data-theme="dark"] body main button:active {
  border: 1px solid var(--button-border-hover);
  border-left: 0;
  border-right: 0;
  border-bottom: 0;
}
